import { defineComponent } from "vue";
let store;
import { WNInterface } from "vue-wni";
import { useStore } from "vuex";
export default defineComponent({
    name: "Splash",
    data() {
        return {};
    },
    async created() {
        store = useStore();
        const splashDelayTime = 2000;
        setTimeout(async () => {
            await this.wnFcmToken();
            await this.$storage.getItem("accessToken") && await this.$storage.getItem('userId') ?
                await this.pushData() : await this.goLoginPage();
        }, splashDelayTime);
    },
    methods: {
        async pushData() {
            //서버로 로그인 데이터 보내기
            let fcmToken = "";
            if (this.$storage.getItem("fcmToken") === false || this.$storage.getItem("fcmToken") === '' || this.$storage.getItem("fcmToken") === null) {
                fcmToken = "notToken";
            }
            else {
                fcmToken = await this.$storage.getItem("fcmToken");
            }
            const VALUE = await store.dispatch("UserInfo/getUserInfo", JSON.stringify({ userId: await this.$storage.getItem('userId'), fcmToken: fcmToken }));
            if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                await this.$storage.setItem('accessToken', VALUE.datas[0]['accessToken'], { expire: '90d' });
                //자동 로그인
                await this.$router.replace({ name: 'Home' });
            }
            else if (VALUE === 'tokenExpire') {
                const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: await this.$storage.getItem('userId') }));
                if (VALUE !== "error" && VALUE !== "notToken" && VALUE !== "tokenExpire") {
                    await this.pushData();
                }
                else {
                    await this.$router.replace({ name: 'Login' });
                }
            }
            else {
                //실패시 로그인 페이지로 이동
                await this.$router.replace({ name: 'Login' });
            }
        },
        goLoginPage() {
            this.$router.replace({ name: 'Login' });
        },
        async wnFcmToken() {
            await WNInterface.execute("wnFcmToken", {
                callback: WNInterface.cb(async (data) => {
                    if (data.fcm) {
                        await this.$storage.setItem('fcmToken', data.fcm, { expire: '90d' });
                    }
                    else {
                        let fcmToken = "notToken";
                        await this.$storage.setItem('fcmToken', fcmToken, { expire: '90d' });
                    }
                })
            });
        },
    }
});
